.card {
  border-color: transparent;
  transition: all 1s linear;
}
.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s linear;
}

.card:hover {
  border: 0.04rem solid rgba(0, 0, 0, 0.2);
  box-shadow: 2px 20x 5px 0px rgba(0, 0, 0, 0.2);
}
.card-footer:hover {
  background: rgba(247, 247, 247);
}

.img-container {
  position: relative;
  overflow: hidden;
}

.card-img-top {
  transition: all 1s linear;
}

.img-container:hover .card-img-top {
  transform: scale(1.2);
}
.cart-btn {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.2rem 0.4 rem;
  background: var(--lightBlue);
  border: none;
  color: var(--mainWhite);
  font-size: 1.4rem;
  border-radius: 0.5rem 0 0 0;
}
.cart-btn:hover {
  color: var(--mainBlue);
  cursor: pointer;
}
